* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
}

code {
  font-family: 'Courier New', Courier, monospace;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.header {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 30px;
  margin-bottom: 30px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
}

.header h1 {
  font-size: 2.5rem;
  color: #667eea;
  margin-bottom: 10px;
  font-weight: 700;
}

.header p {
  color: #666;
  font-size: 1.1rem;
}

.content-wrapper {
  display: grid;
  grid-template-columns: 280px 1fr;
  gap: 30px;
}

.sidebar {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 20px;
  height: fit-content;
  position: sticky;
  top: 20px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
}

.sidebar h3 {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.nav-item {
  padding: 12px 16px;
  margin: 6px 0;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  color: #555;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 8px;
  border: none;
  background: transparent;
  width: 100%;
  text-align: left;
}

.nav-item:hover {
  background: #f0f0f0;
  transform: translateX(4px);
}

.nav-item.active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  font-weight: 600;
}

.main-content {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 40px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
}

.tutorial-section {
  margin-bottom: 40px;
}

.tutorial-section h2 {
  color: #333;
  font-size: 2rem;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.tutorial-section h3 {
  color: #555;
  font-size: 1.5rem;
  margin: 25px 0 15px 0;
}

.tutorial-section p {
  color: #666;
  line-height: 1.8;
  margin-bottom: 15px;
  font-size: 1.05rem;
}

.tutorial-section ul {
  margin-left: 25px;
  margin-bottom: 15px;
}

.tutorial-section li {
  color: #666;
  line-height: 1.8;
  margin-bottom: 8px;
}

.code-example {
  margin: 25px 0;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.code-header {
  background: #f7f7f7;
  padding: 12px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #e0e0e0;
}

.code-header span {
  color: #666;
  font-weight: 600;
  font-size: 0.9rem;
}

.run-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 8px 20px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 6px;
  transition: all 0.2s;
}

.run-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

.run-button:active {
  transform: translateY(0);
}

.output-section {
  background: #1e1e1e;
  color: #d4d4d4;
  padding: 20px;
  border-radius: 0 0 12px 12px;
  font-family: 'Courier New', Courier, monospace;
  min-height: 60px;
}

.output-label {
  color: #4ec9b0;
  font-weight: 600;
  margin-bottom: 10px;
  display: block;
}

.output-content {
  color: #ce9178;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.info-box {
  background: #e3f2fd;
  border-left: 4px solid #2196f3;
  padding: 15px 20px;
  border-radius: 8px;
  margin: 20px 0;
}

.info-box p {
  color: #1565c0;
  margin: 0;
}

.warning-box {
  background: #fff3e0;
  border-left: 4px solid #ff9800;
  padding: 15px 20px;
  border-radius: 8px;
  margin: 20px 0;
}

.warning-box p {
  color: #e65100;
  margin: 0;
}

.success-box {
  background: #e8f5e9;
  border-left: 4px solid #4caf50;
  padding: 15px 20px;
  border-radius: 8px;
  margin: 20px 0;
}

.success-box p {
  color: #2e7d32;
  margin: 0;
}

@media (max-width: 968px) {
  .content-wrapper {
    grid-template-columns: 1fr;
  }
  
  .sidebar {
    position: relative;
    top: 0;
  }
  
  .header h1 {
    font-size: 2rem;
  }
  
  .main-content {
    padding: 25px;
  }
}
